.mindmap-container {
    width: 100vw;
    height: 100vh;

    .mindmap-canvas {
        opacity: 0;

        border: 1px solid white;

        margin: -1px;

        height: calc(100% - 2px);
        height: calc(100% - 2px);
    }

    .mindmap-canvas-loaded {
        animation: 0.5s scaleIn ease-in 3s, 1s fadeIn ease-in;

        animation-delay: 3s;
        animation-fill-mode: forwards;
    }

    .back-button {
        position: absolute;

        top: 40px;
        left: 50%;

        z-index: 1;

        color: white;

        font-size: 20px;

        cursor: pointer;

        animation: 2s fadeIn ease-in 0.5s;

        animation-duration: 0.5s;

        animation-fill-mode: forwards;
        animation-timing-function: ease-in;

        opacity: 0;
    }

    .loading-title {
        font-family: MadeBlack;

        color: white;

        position: absolute;

        left: 50%;
        bottom: 50%;

        z-index: 1;

        transform: translate(-50%, -50%);

        text-transform: uppercase;
    }

    .resistance-title {
        font-family: MadeBlack;

        color: white;
        opacity: 0;

        position: absolute;

        left: 50%;
        bottom: 50%;

        z-index: 1;

        transform: translate(-50%, -50%);

        animation: 0.5s moveDown ease-in 3s, 2s fadeIn ease-in 0.5s;

        animation-duration: 0.5s;

        animation-fill-mode: forwards;
        animation-timing-function: ease-in;
    }
}

@keyframes scaleIn {
    0% {
        margin: 0;

        width: 100%;
        height: 100%;
    }

    100% {
        margin: 20px;

        width: calc(100% - 40px);
        height: calc(100% - 40px);
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes moveDown {
    0% {
        bottom: 50%;
        transform: translate(-50%, -50%);
    }

    100% {
        bottom: 0;
        transform: translate(-50%, -100%);
    }
}